type EventHandler = (e: Event, pos: { x: number, y: number }) => void;

export const addScrollEventListener = (handler: EventHandler) => {
    let last_known_y = 0;
    let last_known_x = 0;
    let ticking = false;

    function handleScrollEvent(e: any) {
        last_known_y = window.scrollY;
        last_known_x = window.scrollX;

        if (!ticking) {
            window.requestAnimationFrame(function () {
                handler(e, { y: last_known_y, x: last_known_x });
                ticking = false;
            });

            ticking = true;
        }
    }

    window.addEventListener('scroll', handleScrollEvent);
    return handleScrollEvent;
}

export const removeScrollEventListener = (handler: any) => {
    window.removeEventListener('scroll', handler);
}