import * as React from 'react';
import { motion } from 'framer-motion';
import { useScrollPosition } from '@n8tb1t/use-scroll-position';


export default () => {

	const [y, setY] = React.useState(0);

	useScrollPosition(({ prevPos, currPos }: any) => {
		if (prevPos.y !== currPos.y) {
			if (-currPos.y < 500) {
				setY(-currPos.y);
			}
		}
	});

	return (
		<motion.div
			className="hero hero-homepage is-primary is-fullheight"
			style={{ backgroundPositionY: y / 2 }}
		>
			<Content />
		</motion.div>
	);
}

const Content = () =>
	<div className="hero-body">
		<div className="container">
			<div className="logo-container">
				<ul className="logo">
					<li className="logo-image title">GEB</li>
					<li className="logo-description">
						<p>Generating</p>
						<p>Global</p>
						<p>Environmental</p>
						<p>Benefits</p>
					</li>
				</ul>
			</div>
			<p className="subtitle tagline"><span><span>Integrating Biodiversity, Climate change and Desertification consideration in Economic decision making
				in Pakistan</span></span>
			</p>
		</div>
	</div>

