import * as React from 'react';

const dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };


type NewsItem = {
	title: string,
	date: Date,
	content: string,
	category: string,
	image: string,
}

export default ({item}: {item: NewsItem}) => (
	<div className='columns news-outline'>
		<div 
			className='column news-outline-image'
			style={{
				backgroundImage: `url(${item.image})`
			}}>
			<span className="tag is-dark">{item.category}</span>
			<br />
			<span className="tag is-dark is-size-7 news-date">{item.date.toLocaleDateString("en-US", dateOptions)}</span>
		</div>
		<div className='column news-outline-description'>
			<h3 className="subtitle is-4">
				<a href="#">
					{item.title}
				</a>
			</h3>
			<p className='content'>{item.content}</p>
		</div>
	</div>
);