import { addScrollEventListener } from './lib/scroll';
import * as React from 'react';
import * as ReactDOM from "react-dom";
import NewsSlider from './components/news-outline';
import Hero from './components/hero';

// Config
const NavbarOpaqueBreakPointY = 200; // in pixels
const Navbar = document.getElementById('main_navbar');



window.addEventListener('DOMContentLoaded', (event) => {


    // add transparency to navbar when on homepage
    if (window.scrollY === 0) {
        Navbar.classList.add("navbar-transparent");
    }

    // animate homepage navbar
    addScrollEventListener((_, { y }) => {
        if (y > NavbarOpaqueBreakPointY) {
            Navbar.classList.contains("navbar-transparent") ?
                Navbar.classList.remove("navbar-transparent") : null;
        } else {
            !Navbar.classList.contains("navbar-transparent") ?
                Navbar.classList.add("navbar-transparent") : null;
        }
    });


    // News section
    ReactDOM.render(NewsSlider, document.getElementById("news_outline"));

    // Hero section
    console.log("inserting hero into the dom");
    ReactDOM.render(<Hero />, document.getElementById("hero_app"));
});